import React from 'react'
import { useEffect , useState} from 'react';

export default function Pagination({mostrarPerrosPag,max,arrayPag, setCurrentPage}){


    useEffect(()=>{
        max();
    })

    function pag(a){
 
        return (
        <input
            key={a}
            id={"paginadoNum"+a}
            className="inputPag"
            type="button"
            onClick={(e)=>todo(e)}
            value={a}
        />)
    }

    function todo(e){
        if(e.target.value === "PrevPag") return setCurrentPage((e)=>{
            if(e > 1) return e-1
        })
        setCurrentPage(Number(e.target.value))
    }

    return (
        <>
            <form id="formularioPaginacion" onSubmit={(e) => {
                e.preventDefault();
            }}>
                <input
                    className="PrevPag"
                    type="submit"
                    value="PrevPag"
                />
                {arrayPag.length === 0 ? <p>Cargando Paginado...</p>: arrayPag.map((a)=>{
                    return (pag(a))
                })}
                <input
                    className="NextPag"
                    type="submit"
                    value="NextPag"
                />
            </form>
        </>
    )
}